___CSS_LOADER_EXPORT___.push([module.id, "\n.expandContent[data-v-05250f06]:hover {\n    background-color: #151233;\n    color: white\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/export/exportJobSteps.vue"],"names":[],"mappings":";AAiFA;IACI,yBAAyB;IACzB;AACJ","sourcesContent":["<template>\n\n    <div v-for=\"artifact in artifacts\" style=\"display:flex\">\n        <i class=\"mdi mdi-monitor artifactIcons\" @click=\"downloadArtifact(artifact.ID, artifact.name)\"></i>\n    </div>\n    <div v-for=\"step in steps\" :key=\"step.ID\" v-bind:value=\"step.ID\">\n        <div data-bs-toggle=\"collapse\" role=\"button\" aria-expanded=\"false\" aria-controls=\"multiCollapseExample2131\"\n            @click=\"chooseStep(step.ID)\">\n            <div class='expandContent'>\n                <i style='font-size: 16px;' :class=\"step.isOpen ? 'mdi mdi-chevron-down' : 'mdi mdi-chevron-right'\"></i>\n                <i style=\"color:#149911; font-size: 16px; padding-left: 15px;\"\n                    class='mdi mdi-check-circle-outline'></i>{{ step.name }}\n            </div>\n\n            <div v-if=\"step.isOpen\">\n                <p v-for=\"log in step.logs\">{{ log }}</p>\n            </div>\n        </div>\n    </div>\n\n</template>\n\n<script setup lang=\"ts\">import { container } from 'tsyringe';\nimport { ref } from 'vue';\nimport 'bootstrap/js/dist/collapse'\nimport { IExportJobArtifactInfoItem, IExportJobItem, IExportJobStepItem } from '../../../../domain/model/models';\nimport { IListExportJobStepLogs } from '../../../../domain/useCase/IListExportJobStepLogs';\nimport { IListExportJobSteps } from '../../../../domain/useCase/IListExportJobSteps';\nimport { IUseCaseExecutor } from '../../../../domain/useCase/IUseCaseExecutor';\nimport { IViewModelManager } from '../../../../domain/viewModel/IViewModelManager';\nimport { IListExportJobArtifacts } from '../../../../domain/useCase/IListExportJobArtifacts';\nimport { IGetExportJobArtifact } from '../../../../domain/useCase/IGetExportJobArtifact';\ninterface IExtentedStepItem extends IExportJobStepItem {\n    isOpen?: boolean\n}\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst props = defineProps<{ job: IExportJobItem }>();\nconst viewModelManager = container.resolve<IViewModelManager>(IViewModelManager);\nconst steps = ref<Array<IExtentedStepItem>>([]);\nconst artifacts = ref<Array<IExportJobArtifactInfoItem>>([]);\n\n\n\n\nexecutor.execute(async () => {\n    await executor.executeUseCase<IListExportJobSteps>(IListExportJobSteps, { jobID: props.job.jobID });\n    const currentJob = viewModelManager.findObject(props.job.jobID) as IExportJobItem\n    steps.value = currentJob.steps!\n    if (props.job.status == 'success') {\n        await executor.executeUseCase<IListExportJobArtifacts>(IListExportJobArtifacts, { jobID: props.job.jobID });\n        artifacts.value = currentJob.artifacts!\n        debugger\n    }\n\n}, { loading: true });\n\n\nfunction chooseStep(stepID: string) {\n    const currentStep = steps.value.find(i => i.ID == stepID)!;\n    if (currentStep.isOpen) {\n        currentStep.isOpen = false;\n        return;\n    }\n    executor.execute(async () => {\n        await executor.executeUseCase<IListExportJobStepLogs>(IListExportJobStepLogs, { stepID });\n        currentStep.isOpen = true;\n    }, { loading: true });\n}\n\nfunction downloadArtifact(artifactID: string, artifactName: string) {\n    debugger\n        executor.execute(async () => {\n        await executor.executeUseCase<IGetExportJobArtifact>(IGetExportJobArtifact, { artifactID, artifactName });\n    }, { loading: true });\n    console.log('dada')\n}\n\n</script>\n\n\n<style scoped>\n.expandContent:hover {\n    background-color: #151233;\n    color: white\n}\n</style>"],"sourceRoot":""}]);
